import React, { useEffect, useState } from 'react';

const Jokes = ({ jk }) => {
    const [jok, setJoke] = useState();
    const [j, setJ] = useState();

    useEffect(() => {
        
        if (jk.includes("</br>")){
            const a = jk.split("</br>");
            setJoke(a[0])
            setJ(a[1]);
        }
        else{
            setJ("");
            setJoke(jk)
        }
    })

    return (
        <>
            <p>
                {jok}
            </p>
            <p>{j}</p>
        </>
    )
}

export default Jokes;